import React, { useState, useEffect } from 'react';
import {
  PageSection,
  Title,
  Card,
  CardBody,
  Button,
  Badge,
  EmptyState,
  EmptyStateActions,
  EmptyStateBody,
  Spinner,
  Alert,
  Modal,
  ModalVariant,
  ModalBody,
  ModalFooter,
  Bullseye,
  Flex,
  FlexItem,
  Gallery,
  GalleryItem,
  Divider,
  Split,
  SplitItem,
  Stack,
  StackItem,
  Label,
  Content
} from '@patternfly/react-core';
import { 
  DownloadIcon, 
  CubesIcon,
  TagIcon,
  CalendarAltIcon,
  UserIcon
} from '@patternfly/react-icons';
import { useNavigate } from 'react-router-dom';
import { authFetch, API_BASE } from '../utils/auth';

interface BuildListItem {
  name: string;
  phase: string;
  message: string;
  requestedBy?: string;
  createdAt: string;
  startTime?: string;
  completionTime?: string;
}

interface BuildDetails {
  name: string;
  phase: string;
  message: string;
  requestedBy?: string;
  artifactURL?: string;
  artifactFileName?: string;
  startTime?: string;
  completionTime?: string;
}

interface BuildParams {
  name: string;
  manifest?: string;
  manifestFileName?: string;
  distro?: string;
  target?: string;
  architecture?: string;
  exportFormat?: string;
  mode?: string;
  automotiveImageBuilder?: string;
  compression?: string;
}

interface CatalogItem {
  name: string;
  phase: string;
  message: string;
  requestedBy?: string;
  createdAt: string;
  startTime?: string;
  completionTime?: string;
  // ImageBuild spec fields
  distro: string;
  target: string;
  architecture: string;
  exportFormat: string;
  mode: string;
  automotiveImageBuilder?: string;
  compression?: string;
  // Status fields
  artifactURL?: string;
  artifactFileName?: string;
  // Additional display fields
  tags: string[];
  downloadCount?: number;
}

const CatalogPage: React.FC = () => {
  const navigate = useNavigate();
  const [catalogItems, setCatalogItems] = useState<CatalogItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedItem, setSelectedItem] = useState<CatalogItem | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [downloadingItem, setDownloadingItem] = useState<string | null>(null);

  // Mock data for initial layout - will be replaced with real API calls
  const mockCatalogItems: CatalogItem[] = [
    {
      name: 'rhel9-automotive-base',
      phase: 'Completed',
      message: 'Build completed successfully',
      requestedBy: 'automotive-team',
      createdAt: '2024-01-15T10:30:00Z',
      startTime: '2024-01-15T10:32:00Z',
      completionTime: '2024-01-15T11:45:00Z',
      distro: 'rhel-9',
      target: 'qemu',
      architecture: 'x86_64',
      exportFormat: 'qcow2',
      mode: 'image',
      automotiveImageBuilder: 'registry.redhat.io/automotive/image-builder:latest',
      compression: 'gzip',
      artifactURL: '/v1/builds/rhel9-automotive-base/artifacts',
      artifactFileName: 'rhel9-automotive-base.qcow2',
      tags: ['rhel', 'automotive', 'base'],
      downloadCount: 127
    },
    {
      name: 'cs9-development-env',
      phase: 'Completed',
      message: 'Development environment build completed',
      requestedBy: 'dev-team',
      createdAt: '2024-01-20T14:45:00Z',
      startTime: '2024-01-20T14:47:00Z',
      completionTime: '2024-01-20T16:23:00Z',
      distro: 'centos-stream-9',
      target: 'qemu',
      architecture: 'aarch64',
      exportFormat: 'raw',
      mode: 'image',
      automotiveImageBuilder: 'registry.redhat.io/automotive/image-builder:latest',
      compression: 'lz4',
      artifactURL: '/v1/builds/cs9-development-env/artifacts',
      artifactFileName: 'cs9-development-env.raw',
      tags: ['centos', 'development', 'tools'],
      downloadCount: 89
    },
    {
      name: 'fedora-iot-edge',
      phase: 'Completed',
      message: 'IoT edge build completed',
      requestedBy: 'iot-team',
      createdAt: '2024-02-01T09:15:00Z',
      startTime: '2024-02-01T09:17:00Z',
      completionTime: '2024-02-01T10:42:00Z',
      distro: 'fedora-iot',
      target: 'edge-simplified-installer',
      architecture: 'x86_64',
      exportFormat: 'image',
      mode: 'image',
      automotiveImageBuilder: 'registry.redhat.io/automotive/image-builder:latest',
      compression: 'gzip',
      artifactURL: '/v1/builds/fedora-iot-edge/artifacts',
      artifactFileName: 'fedora-iot-edge.img',
      tags: ['fedora', 'iot', 'edge'],
      downloadCount: 203
    }
  ];

  const fetchCatalogItems = async () => {
    try {
      setLoading(true);
      setError(null);
      
      // Fetch all builds from the API
      const response = await authFetch(`${API_BASE}/v1/builds`);
      if (!response.ok) {
        throw new Error(`Failed to fetch builds: ${response.status}`);
      }
      
      const buildList: BuildListItem[] = await response.json();
      
      // Filter for completed builds and fetch their details
      const completedBuilds = buildList.filter(build => build.phase === 'Completed');
      
      const catalogPromises = completedBuilds.map(async (build) => {
        try {
          // Fetch build details to get artifact info
          const detailsResponse = await authFetch(`${API_BASE}/v1/builds/${build.name}`);
          const details: BuildDetails = detailsResponse.ok ? await detailsResponse.json() : {};
          
          // Fetch build template/params to get ImageBuild spec info
          const paramsResponse = await authFetch(`${API_BASE}/v1/builds/${build.name}/template`);
          const params: BuildParams = paramsResponse.ok ? await paramsResponse.json() : {};
          
          // Create catalog item from build data
          const catalogItem: CatalogItem = {
            name: build.name,
            phase: build.phase,
            message: build.message,
            requestedBy: build.requestedBy,
            createdAt: build.createdAt,
            startTime: build.startTime,
            completionTime: build.completionTime,
            distro: params.distro || 'unknown',
            target: params.target || 'unknown',
            architecture: params.architecture || 'unknown',
            exportFormat: params.exportFormat || 'unknown',
            mode: params.mode || 'unknown',
            automotiveImageBuilder: params.automotiveImageBuilder,
            compression: params.compression,
            artifactURL: details.artifactURL,
            artifactFileName: details.artifactFileName,
            tags: generateTagsFromBuild(params),
            downloadCount: 0 // TODO: Track download count
          };
          
          return catalogItem;
        } catch (err) {
          console.error(`Error fetching details for build ${build.name}:`, err);
          return null;
        }
      });
      
      const catalogItems = (await Promise.all(catalogPromises)).filter(item => item !== null) as CatalogItem[];
      setCatalogItems(catalogItems);
      
    } catch (err) {
      console.error('Error fetching catalog items:', err);
      setError('Failed to load catalog items');
      // Fall back to mock data on error
      setCatalogItems(mockCatalogItems);
    } finally {
      setLoading(false);
    }
  };
  
  const generateTagsFromBuild = (params: BuildParams): string[] => {
    const tags: string[] = [];
    
    if (params.distro) {
      if (params.distro.includes('rhel')) tags.push('rhel');
      else if (params.distro.includes('centos')) tags.push('centos');
      else if (params.distro.includes('fedora')) tags.push('fedora');
      else tags.push(params.distro);
    }
    
    if (params.mode) tags.push(params.mode);
    if (params.target) tags.push(params.target);
    if (params.exportFormat) tags.push(params.exportFormat);
    
    return tags;
  };

  useEffect(() => {
    fetchCatalogItems();
  }, []);

  const handleDownload = async (item: CatalogItem) => {
    try {
      setDownloadingItem(item.name);
      
      if (item.artifactURL && item.artifactFileName) {
        // Create a download link using the artifact URL
        const downloadUrl = `${API_BASE}${item.artifactURL}/${item.artifactFileName}`;
        
        // Create a temporary anchor element to trigger download
        const link = document.createElement('a');
        link.href = downloadUrl;
        link.download = item.artifactFileName;
        link.style.display = 'none';
        
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        
        console.log('Download initiated for:', item.name);
      } else {
        console.warn('No artifact URL available for:', item.name);
      }
      
    } catch (err) {
      console.error('Error downloading item:', err);
    } finally {
      setDownloadingItem(null);
    }
  };

  const handleUseAsTemplate = (item: CatalogItem) => {
    // Navigate to create build page with this item as a template
    navigate('/create', { 
      state: { 
        template: {
          name: `${item.name}-template`,
          distro: item.distro,
          target: item.target,
          architecture: item.architecture,
          exportFormat: item.exportFormat,
          mode: item.mode,
          automotiveImageBuilder: item.automotiveImageBuilder,
          compression: item.compression,
          templateId: item.name
        }
      }
    });
  };

  const openItemDetails = (item: CatalogItem) => {
    setSelectedItem(item);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedItem(null);
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  };

  const getBadgeColor = (tag: string): "grey" | "red" | "blue" | "purple" | "green" | "orange" | "teal" | "orangered" | "yellow" | undefined => {
    const colorMap: { [key: string]: "grey" | "red" | "blue" | "purple" | "green" | "orange" | "teal" | "orangered" | "yellow" } = {
      'rhel': 'red',
      'centos': 'blue',
      'fedora': 'purple',
      'development': 'green',
      'base': 'grey',
      'iot': 'orange',
      'automotive': 'teal'
    };
    return colorMap[tag] || 'grey';
  };

  if (loading) {
    return (
      <PageSection>
        <Bullseye>
          <Spinner size="xl" />
        </Bullseye>
      </PageSection>
    );
  }

  if (error) {
    return (
      <PageSection>
        <Alert variant="danger" title="Error loading catalog">
          {error}
        </Alert>
      </PageSection>
    );
  }

  return (
    <>
      <PageSection>
        <Stack hasGutter>
          <StackItem>
            <Title headingLevel="h1" size="2xl">
              <CubesIcon className="pf-v6-u-mr-sm" />
              Image Catalog
            </Title>
          </StackItem>
          <StackItem>
            <Content component="p">
              Browse and download published image builds to use as templates for your projects.
            </Content>
          </StackItem>
        </Stack>
      </PageSection>

      <PageSection>
        {catalogItems.length === 0 ? (
          <EmptyState>
            <CubesIcon />
            <Title headingLevel="h4" size="lg">
              No catalog items found
            </Title>
            <EmptyStateBody>
              There are no published image builds available in the catalog yet.
            </EmptyStateBody>
            <EmptyStateActions>
              <Button variant="primary" onClick={() => navigate('/create')}>
                Create New Build
              </Button>
            </EmptyStateActions>
          </EmptyState>
        ) : (
          <Gallery hasGutter minWidths={{ default: '300px' }} maxWidths={{ default: '400px' }}>
            {catalogItems.map((item) => (
              <GalleryItem key={item.name}>
                <Card 
                  onClick={() => openItemDetails(item)}
                  className="pf-m-clickable"
                >
                  <CardBody>
                    <Stack hasGutter>
                      <StackItem>
                        <Split hasGutter>
                          <SplitItem isFilled>
                            <Title headingLevel="h3" size="lg">
                              {item.name}
                            </Title>
                          </SplitItem>
                          <SplitItem>
                            <Badge color="blue">{item.phase}</Badge>
                          </SplitItem>
                        </Split>
                      </StackItem>
                      
                      <StackItem>
                        <Content component="small">
                          {item.message}
                        </Content>
                      </StackItem>
                      
                      <StackItem>
                        <Split hasGutter>
                          <SplitItem>
                            <Content component="small">
                              <strong>Distro:</strong> {item.distro}
                            </Content>
                          </SplitItem>
                          <SplitItem>
                            <Content component="small">
                              <strong>Target:</strong> {item.target}
                            </Content>
                          </SplitItem>
                        </Split>
                      </StackItem>
                      
                      <StackItem>
                        <Split hasGutter>
                          <SplitItem>
                            <Content component="small">
                              <strong>Format:</strong> {item.exportFormat}
                            </Content>
                          </SplitItem>
                          <SplitItem>
                            <Content component="small">
                              <strong>Mode:</strong> {item.mode}
                            </Content>
                          </SplitItem>
                        </Split>
                      </StackItem>
                      
                      <StackItem>
                        <Flex spaceItems={{ default: 'spaceItemsXs' }}>
                          {item.tags.map((tag) => (
                            <FlexItem key={tag}>
                              <Label color={getBadgeColor(tag)} icon={<TagIcon />}>
                                {tag}
                              </Label>
                            </FlexItem>
                          ))}
                        </Flex>
                      </StackItem>
                      
                      <StackItem>
                        <Split>
                          <SplitItem>
                            <Content component="small">
                              <CalendarAltIcon className="pf-v6-u-mr-xs" />
                              {formatDate(item.createdAt)}
                            </Content>
                          </SplitItem>
                          <SplitItem isFilled />
                          <SplitItem>
                            <Content component="small">
                              {item.downloadCount} downloads
                            </Content>
                          </SplitItem>
                        </Split>
                      </StackItem>
                      
                      <StackItem>
                        <Split>
                          <SplitItem>
                            <Content component="small">
                              <strong>Arch:</strong> {item.architecture}
                            </Content>
                          </SplitItem>
                          <SplitItem isFilled />
                          <SplitItem>
                            <Content component="small">
                              <strong>Compression:</strong> {item.compression}
                            </Content>
                          </SplitItem>
                        </Split>
                      </StackItem>
                      
                      <Divider />
                      
                      <StackItem>
                        <Flex>
                          <FlexItem>
                            <Button
                              variant="primary"
                              size="sm"
                              onClick={(e) => {
                                e.stopPropagation();
                                handleUseAsTemplate(item);
                              }}
                            >
                              Use as Template
                            </Button>
                          </FlexItem>
                          <FlexItem>
                            <Button
                              variant="secondary"
                              size="sm"
                              icon={<DownloadIcon />}
                              isLoading={downloadingItem === item.name}
                              onClick={(e) => {
                                e.stopPropagation();
                                handleDownload(item);
                              }}
                            >
                              Download
                            </Button>
                          </FlexItem>
                        </Flex>
                      </StackItem>
                    </Stack>
                  </CardBody>
                </Card>
              </GalleryItem>
            ))}
          </Gallery>
        )}
      </PageSection>

      {/* Item Details Modal */}
      <Modal
        variant={ModalVariant.medium}
        title={selectedItem?.name || ''}
        isOpen={isModalOpen}
        onClose={closeModal}
      >
        <ModalBody>
          {selectedItem && (
            <Stack hasGutter>
              <StackItem>
                <Content component="p">
                  {selectedItem.message}
                </Content>
              </StackItem>
              
              <StackItem>
                <Split hasGutter>
                  <SplitItem>
                    <strong>Distro:</strong> {selectedItem.distro}
                  </SplitItem>
                  <SplitItem>
                    <strong>Target:</strong> {selectedItem.target}
                  </SplitItem>
                  <SplitItem>
                    <strong>Architecture:</strong> {selectedItem.architecture}
                  </SplitItem>
                </Split>
              </StackItem>
              
              <StackItem>
                <Split hasGutter>
                  <SplitItem>
                    <strong>Export Format:</strong> {selectedItem.exportFormat}
                  </SplitItem>
                  <SplitItem>
                    <strong>Build Mode:</strong> {selectedItem.mode}
                  </SplitItem>
                  <SplitItem>
                    <strong>Compression:</strong> {selectedItem.compression}
                  </SplitItem>
                </Split>
              </StackItem>
              
              <StackItem>
                <Split hasGutter>
                  <SplitItem>
                    <strong>Phase:</strong> {selectedItem.phase}
                  </SplitItem>
                  <SplitItem>
                    <strong>Downloads:</strong> {selectedItem.downloadCount || 0}
                  </SplitItem>
                  <SplitItem>
                    <strong>Created:</strong> {formatDate(selectedItem.createdAt)}
                  </SplitItem>
                </Split>
              </StackItem>
              
              {selectedItem.completionTime && (
                <StackItem>
                  <strong>Completed:</strong> {formatDate(selectedItem.completionTime)}
                </StackItem>
              )}
              
              {selectedItem.artifactFileName && (
                <StackItem>
                  <strong>Artifact:</strong> {selectedItem.artifactFileName}
                </StackItem>
              )}
              
              {selectedItem.requestedBy && (
                <StackItem>
                  <Content>
                    <UserIcon className="pf-v6-u-mr-xs" />
                    <strong>Requested By:</strong> {selectedItem.requestedBy}
                  </Content>
                </StackItem>
              )}
              
              <StackItem>
                <Content><strong>Tags:</strong></Content>
                <Flex spaceItems={{ default: 'spaceItemsXs' }} className="pf-v6-u-mt-xs">
                  {selectedItem.tags.map((tag) => (
                    <FlexItem key={tag}>
                      <Label color={getBadgeColor(tag)} icon={<TagIcon />}>
                        {tag}
                      </Label>
                    </FlexItem>
                  ))}
                </Flex>
              </StackItem>
            </Stack>
          )}
        </ModalBody>
        <ModalFooter>
          <Button
            variant="primary"
            onClick={() => {
              if (selectedItem) {
                handleUseAsTemplate(selectedItem);
                closeModal();
              }
            }}
          >
            Use as Template
          </Button>
          <Button
            variant="secondary"
            icon={<DownloadIcon />}
            isLoading={downloadingItem === selectedItem?.name}
            onClick={() => {
              if (selectedItem) {
                handleDownload(selectedItem);
              }
            }}
          >
            Download
          </Button>
          <Button variant="link" onClick={closeModal}>
            Close
          </Button>
        </ModalFooter>
      </Modal>
    </>
  );
};

export default CatalogPage;
